.stuInfoContent {
  padding: 54px 20px;
}
.stuInfoContent .avatarBox {
  text-align: center;
}
.stuInfoContent .avatarBox img {
  width: 53px;
  height: 53px;
}
.stuInfoContent .avatarBox h4 {
  font-size: 16px;
  color: #485465;
}
.stuInfoContent .avatarBox p {
  margin-bottom: 20px;
  color: #121a2c;
}
.stuInfoContent > p {
  overflow: hidden;
}
.stuInfoContent > p span {
  display: block;
  width: calc(100% - 90px);
  float: right;
  text-align: right;
}
